@import url("https://fonts.googleapis.com/css2?family=Ranchers&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ranchers&family=Roboto+Condensed:wght@300;400&display=swap");

.aboutme-photo {
  height: 36em;
  width: 29em;
}
.aboutme-container-photo {
  display: flex;
  background-image: url("../../assets/images/wallpaper.jpg");
  background-size: cover;
  justify-content: flex-start;
}

.aboutme-content-information {
  display: flex;
  align-items: center;
}

.aboutme-information h2 {
  margin-bottom: 25px;
  font-family: "Ranchers", cursive;
}

.aboutme-information p {
  font-family: "Ranchers", cursive;
  font-family: "Roboto Condensed", sans-serif;
}

.aboutme-information {
  margin-left: 20em;
  margin-left: 15%;
  margin-right: 15%;
}

@media (max-width: 425px) {
  .aboutme-container-photo {
    background-attachment: fixed;
    position: relative;
  }

  .aboutme-information {
    margin-top: 3em;
    margin-bottom: 3em;
  }
}
