@import url("https://fonts.googleapis.com/css2?family=Ranchers&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ranchers&family=Roboto+Condensed:wght@300;400&display=swap");

.skills-container {
  padding-top: 25px !important;
  padding-bottom: 25px;
  background-image: url("../../assets/images/wallpaper-2.jpg");
  background-position: center center;
  background-size: cover;
  position: relative;
  color: #ffffff;
  background-attachment: fixed;
}

.skills-container::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(17, 59, 54, 0.4);
}

.skills-card {
  display: flex;
  justify-content: center;
  margin-bottom: 25px;
}

.container h2 {
  margin-bottom: 25px;
  font-family: "Ranchers", cursive;
  z-index: 1;
  position: relative;
  color: #000000;
}
