@import url("https://fonts.googleapis.com/css2?family=Ranchers&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ranchers&family=Roboto+Condensed:wght@300;400&display=swap");

.contactme-photo {
  height: 36em;
  width: 29em;
}
.contactme-container-photo {
  display: flex;
  background-image: url("../../assets/images/wallpaper-3.jpg");
  background-size: cover;
  justify-content: flex-start;
  background-position: center center;
}

.contactme-content-information {
  display: flex;
  align-items: center;
  padding-top: 25px;
}

.contactme-information h2 {
  margin-bottom: 25px;
  font-family: "Ranchers", cursive;
}

.contactme-information p {
  font-family: "Ranchers", cursive;
  font-family: "Roboto Condensed", sans-serif;
}

.contactme-information {
  margin-left: 20em;
  margin-left: 15%;
  margin-right: 15%;
}

.contactme-row-icon {
  font-size: 6em;
}

.contactme-icon {
  display: flex;
  justify-content: center;
  margin-right: 30px;
  margin-bottom: 15px;
  background: #224f9b;
}

.contactme-icon svg {
  color: #ffffff;
}

.contactme-icon svg:hover {
  color: #afdde8;
  transition: 0.5s;
}

@media (max-width: 425px) {
  .contactme-container-photo {
    background-attachment: fixed;
    position: relative;
  }

  .contactme-information {
    margin-top: 3em;
    margin-bottom: 3em;
  }
}
