.aboutme-photo {
  height: 36em;
  width: 29em;
}
.aboutme-container-photo {
  display: flex;
  background-image: url(/richardrb-page/static/media/wallpaper.690cf657.jpg);
  background-size: cover;
  justify-content: flex-start;
}

.aboutme-content-information {
  display: flex;
  align-items: center;
}

.aboutme-information h2 {
  margin-bottom: 25px;
  font-family: "Ranchers", cursive;
}

.aboutme-information p {
  font-family: "Ranchers", cursive;
  font-family: "Roboto Condensed", sans-serif;
}

.aboutme-information {
  margin-left: 20em;
  margin-left: 15%;
  margin-right: 15%;
}

@media (max-width: 425px) {
  .aboutme-container-photo {
    background-attachment: fixed;
    position: relative;
  }

  .aboutme-information {
    margin-top: 3em;
    margin-bottom: 3em;
  }
}

.contactme-photo {
  height: 36em;
  width: 29em;
}
.contactme-container-photo {
  display: flex;
  background-image: url(/richardrb-page/static/media/wallpaper-3.539f2684.jpg);
  background-size: cover;
  justify-content: flex-start;
  background-position: center center;
}

.contactme-content-information {
  display: flex;
  align-items: center;
  padding-top: 25px;
}

.contactme-information h2 {
  margin-bottom: 25px;
  font-family: "Ranchers", cursive;
}

.contactme-information p {
  font-family: "Ranchers", cursive;
  font-family: "Roboto Condensed", sans-serif;
}

.contactme-information {
  margin-left: 20em;
  margin-left: 15%;
  margin-right: 15%;
}

.contactme-row-icon {
  font-size: 6em;
}

.contactme-icon {
  display: flex;
  justify-content: center;
  margin-right: 30px;
  margin-bottom: 15px;
  background: #224f9b;
}

.contactme-icon svg {
  color: #ffffff;
}

.contactme-icon svg:hover {
  color: #afdde8;
  transition: 0.5s;
}

@media (max-width: 425px) {
  .contactme-container-photo {
    background-attachment: fixed;
    position: relative;
  }

  .contactme-information {
    margin-top: 3em;
    margin-bottom: 3em;
  }
}

.footer-container {
  background: #000000;
  color: #ffffff;
  font-weight: bold;
  font-size: 17px;
  display: flex;
  justify-content: center;
  padding-bottom: 2em;
  padding-top: 2em;
}

.navbar-container {
  padding: 1em 0px;
  color: #ffffff;
  text-decoration: none;
}

.navbar-icon-size {
  font-size: 3em;
  color: red;
}

.navbar-description {
  padding-left: 20px;
  color: #ffffff;
  font-size: 25px;
}

.navbar-items {
  color: #ffffff;
}

.header-viewport {
  height: 100vh;
  background-image: url(/richardrb-page/static/media/my-header.aca77d73.jpg);
  background-position: center center;
  background-size: cover;
  position: relative;
  color: #ffffff;
  background-attachment: fixed;
}

.header-viewport::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
}

.header-main-content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2em;
  height: 75vh;
}

.header-main-content span {
  font-size: 2em;
}

.header-title {
  font-size: 5em;
  font-weight: bold;
}

@media (max-width: 425px) {
  .header-title {
    font-size: 3.1em;
    font-weight: bold;
  }
  .header-main-content span {
    font-size: 1em;
  }
}

.card-container {
  width: 6em;
  text-align: center;
  background: #ffffff;
}

.card-container:hover {
  background-color: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  transition: 0.5s;
}

.skills-container {
  padding-top: 25px !important;
  padding-bottom: 25px;
  background-image: url(/richardrb-page/static/media/wallpaper-2.ce03075d.jpg);
  background-position: center center;
  background-size: cover;
  position: relative;
  color: #ffffff;
  background-attachment: fixed;
}

.skills-container::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(17, 59, 54, 0.4);
}

.skills-card {
  display: flex;
  justify-content: center;
  margin-bottom: 25px;
}

.container h2 {
  margin-bottom: 25px;
  font-family: "Ranchers", cursive;
  z-index: 1;
  position: relative;
  color: #000000;
}

