.header-viewport {
  height: 100vh;
  background-image: url("../../assets/images/my-header.jpg");
  background-position: center center;
  background-size: cover;
  position: relative;
  color: #ffffff;
  background-attachment: fixed;
}

.header-viewport::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
}

.header-main-content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2em;
  height: 75vh;
}

.header-main-content span {
  font-size: 2em;
}

.header-title {
  font-size: 5em;
  font-weight: bold;
}

@media (max-width: 425px) {
  .header-title {
    font-size: 3.1em;
    font-weight: bold;
  }
  .header-main-content span {
    font-size: 1em;
  }
}
